<template>
    <div class="practice page">
      <h1>
        <div>{{ getDiscipline }}</div>
        <div>practice</div>
      </h1>
      <div class="underline"></div>
        <Loader v-if="getLoading" />
        <h3 v-else-if="noData">There are no questions available in {{ getDiscipline }} right now, but they will be added soon.</h3>
        <template v-else v-for="(practice, index) in getData">
          <h3 :key="practice.topic" v-text="`${practice.topic}:`"></h3>
          <div class="qboxes" :key="index">
            <QBox :key="practice.title" :topic="practice.topic" :title="practice.title" :questions="practice.questions.length" />
          </div>
        </template>
    </div>
</template>

<script>
import Loader from "@/components/Loader";
import QBox from "@/components/QBox";

import { mapGetters, mapActions } from "vuex";
import { gsap } from "gsap";
import store from "@/store/index.js";

export default {
  name: "Lessons",
  components: {
    Loader,
    QBox
  },
  methods: {
    ...mapActions(["setData"])
  },
  computed: {
    ...mapGetters(["getDiscipline", "getData", "getLoading", "noData"])
  },
  created() {
    this.setData();
  },
  mounted() {
    gsap.to("h1 div", {
      y: 0,
      opacity: 1,
      duration: 0.5,
      stagger: 0.25
    });
    gsap.from(".underline", { width: 0, duration: 0.5, delay: 0.5 });
    gsap.to(".loader", { y: 0, opacity: 1, duration: 0.5, delay: 1 });
  },
  destroyed() {
    store.commit("noData");
    store.commit("toggleLoading");
  }
};
</script>

<style lang="sass" scoped>
.practice
  .loader
    transform: translateY(2rem)
    opacity: 0
  h3
    font-size: 1.25rem
    margin-top: 2rem
    margin-bottom: 1rem  
  .qboxes
    display: flex
    flex-wrap: wrap
</style>
