<template>
    <div class="home page">
        <h1>
          <div>Revise</div>
          <div>Like a Pro</div>
        </h1>
        <div class="underline"></div>
        <div class="disciplines">
          <DisciplineCard v-for="discipline in disciplines" :key="discipline.title" :title="discipline.title" :imageUrl="discipline.imageUrl" />
        </div>
    </div>
</template>

<script>
import { gsap } from "gsap";
import axios from "axios";
import DisciplineCard from "@/components/DisciplineCard";

export default {
  name: "Home",
  components: {
    DisciplineCard
  },
  data() {
    return {
      disciplines: [
        {
          title: "Anatomy"
        },
        {
          title: "Physiology"
        },
        {
          title: "Pathology"
        },
        {
          title: "Microbiology"
        }
      ]
    };
  },
  created() {
    axios("https://api.ipify.org?format=json").then(response => {
      axios.post(`/api/ip/${response.data.ip}`);
    });
  },
  mounted() {
    gsap.to("h1 div", {
      y: 0,
      opacity: 1,
      duration: 0.5,
      stagger: 0.25
    });
    gsap.from(".underline", { width: 0, delay: 1.5 });
    gsap.to(".discipline", { y: 0, opacity: 1, delay: 2, stagger: 0.25 });
  }
};
</script>

<style lang="sass" scoped>
.home
  padding-bottom: 0
  .disciplines
    display: flex
    flex-wrap: wrap
    margin-top: 2rem
</style>
