<template>
    <div class="sets page">
        <h1>
          <div>Choose</div>
          <div>a set</div>
        </h1>
        <div class="underline"></div>
        <!-- <Loader v-if="getLoading" /> -->
        <!-- <ul>
            <li>
                <router-link to="/exam">
                    <button class="route-btn">
                        All Questions ({{ allQuestions.length }})
                    </button>
                </router-link>
            </li>
            <li v-for="(set, index) in chunkArray" :key="set[0].text">
                <router-link to="/exam">
                    <button class="route-btn" @click="chooseSet(chunkArray[index])">
                      Questions ({{(index+1) + (index * 10) - index }} - {{ isTen(chunkArray, index) ? (index+1) * 10 : index + (index * 10) - index + chunkArray[index].length }})
                    </button>
                </router-link>
            </li>
        </ul> -->
        <div class="setboxes">
          <SetBox v-for="(set, index) in chunkArray"
          :key="index"
          :set="set"
          :title="`Set ${index+1}`"
          :range="`${((10*index)+1)}-${(10*index)+10}`" />
        </div>
    </div>
</template>

<script>
import { gsap } from "gsap";
import { mapGetters, mapActions } from "vuex";
//import Loader from "@/components/Loader.vue";
import SetBox from "@/components/SetBox";

export default {
  name: "Sets",
  components: {
    //Loader
    SetBox
  },
  mounted() {
    gsap.to("h1 div", {
      y: 0,
      opacity: 1,
      duration: 0.5,
      stagger: 0.25
    });
    gsap.from(".underline", { width: 0, delay: 0.5 });
    gsap.to(".setbox", { y: 0, opacity: 1, delay: 1, stagger: 0.25 });
    // gsap.to(".discipline", { y: 0, opacity: 1, delay: 2, stagger: 0.25 });
  },
  methods: {
    ...mapActions(["fetchQuestions", "chooseSet"]),
    isTen(arr, index) {
      if (arr[index].length < 10) {
        return false;
      } else {
        return true;
      }
    }
  },

  computed: {
    ...mapGetters(["getLoading", "getData", "getPractice"]),
    chunkArray() {
      const dataObject = this.getData.find(obj => {
        return (
          obj.topic === this.getPractice.topic &&
          obj.title === this.getPractice.practice
        );
      });
      const myArray = dataObject.questions;
      const chunk_size = 10;

      var index = 0;
      var arrayLength = myArray.length;
      var tempArray = [];

      for (index = 0; index < arrayLength; index += chunk_size) {
        let myChunk = myArray.slice(index, index + chunk_size);
        tempArray.push(myChunk);
      }
      return tempArray;
    }
  },
  created() {
    // this.fetchQuestions();
  }
};
</script>

<style lang="sass" scoped>
.sets
  .setboxes
    display: flex
    flex-wrap: wrap
    margin-top: 2rem
</style>
