<template>
    <div class="review page">
        <h1>
            <div>Review</div>
            <div>your answers</div>
        </h1>
        <div class="underline"></div>
        <div class="question" v-for="(question, index) in getSet" :key="question._id">
            <h3>{{ index + 1 }}. {{ question.text }}</h3>
            <ol>
                <li
                    v-for="choice in question.choices"
                    :key="choice.text"
                    :class="choice.chosen && !choice.correct ? 'wrong' : choice.correct ? 'correct' : ''">
                    {{ choice.text }}
                    <div class="overlay"></div>
                </li>
            </ol>
        </div>
        <router-link to="/">
            <button class="route-btn" @click="reset()">Back to Home</button>
        </router-link>
    </div>
</template>

<script>
import { gsap } from "gsap";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Review",
  mounted() {
    gsap.to("h1 div", {
      y: 0,
      opacity: 1,
      duration: 0.5,
      stagger: 0.25
    });
    gsap.from(".underline", { width: 0, delay: 0.5 });
    gsap.to("h3", { y: 0, opacity: 1, delay: 1 });
    gsap.to("ol li", { y: 0, opacity: 1, delay: 1.25, stagger: 0.25 });
    gsap.to("ol li.correct, ol li.wrong", {
      color: "white",
      delay: 2.5
    });
    gsap.to("ol li .overlay", {
      width: "calc(100% + 2rem)",
      delay: 2.5
    });
  },
  methods: mapActions(["reset"]),
  computed: {
    ...mapGetters(["getSet", "correctQuestions"])
  }
};
</script>

<style lang="sass">
.review
    .question
        margin: 2rem 0 1.5rem
        border-bottom: .25rem solid #5cbfe5
        h3
            transform: translateY(1rem)
            opacity: 0
        ol
            list-style: lower-alpha
            margin-bottom: 1.5rem
            li
                transform: translateY(1rem)
                opacity: 0
                padding: .5rem
                margin-bottom: .5rem
                position: relative
                .overlay
                    position: absolute
                    z-index: -1
                    left: -2rem
                    top: 0
                    width: 0
                    height: 100%
                // &.correct, &.wrong
                //     color: white
                &.correct .overlay
                    background: #388E3C
                &.wrong .overlay
                    background: #D50000
    button
        position: fixed
        left: 0
        bottom: 0
        width: 100%
        font-size: 1rem
        background: #5cbfe5
        color: white
        padding: 1rem
        border: none
</style>
