<template>
    <div class="result page">
        <h1>
            <div>Your practice</div>
            <div>is finished</div>
        </h1>
        <div class="underline"></div>
        <ul>
            <li>
                <div><span>Correct Questions</span></div> <div><span>{{ correctQuestions.length }} / {{ getSet.length }}</span></div>
            </li>
            <li>
                <div><span>Wrong Questions</span></div> <div><span>{{ wrongQuestions.length }} / {{ getSet.length }}</span></div>
            </li>
            <li>
               <div><span>Status</span></div>
               <div>
                   <span v-if="correctQuestions.length >= (getSet.length / 2)">Passed</span>
                   <span v-else>Failed</span>
                   <span>({{ Math.round(((correctQuestions.length * 100) / getSet.length)) }}%)</span></div>
            </li>
        </ul>
        <router-link to="/review">
            <button>Review your answers</button>
        </router-link>
    </div>
</template>

<script>
import { gsap } from "gsap";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Finish",
  mounted() {
    gsap.to("h1 div", {
      y: 0,
      opacity: 1,
      duration: 0.5,
      stagger: 0.25
    });
    gsap.from(".underline", { width: 0, delay: 0.5 });
    gsap.to("ul", { y: 0, opacity: 1, delay: 1 });
    gsap.to("ul li div span", { x: 0, opacity: 1, delay: 1.5, stagger: 0.25 });
  },
  methods: mapActions(["reset"]),
  computed: mapGetters(["getSet", "correctQuestions", "wrongQuestions"])
};
</script>

<style lang="sass">
.result
    ul
        margin: 2rem 0
        border: 1px solid #5cbfe5
        transform: translateY(2rem)
        opacity: 0
        li
            display: flex
            div
                display: flex
                flex-direction: column
                justify-content: center
                padding: 1rem
                border: 1px solid #5cbfe5
                &:first-of-type
                    width: 67%
                &:last-of-type
                    width: 33%
                span
                    tranform: translateX(2rem)
                    opacity: 0
    button
        position: fixed
        font-size: 1rem
        left: 0
        bottom: 0
        width: 100%
        background: #5cbfe5
        color: white
        padding: 1rem
        border: none
</style>

